.App {
  text-align: center;
  min-height: 100vmin;
  font-size: calc(10px + 2vmin);
}

.App-image {
  max-width: 50vmin;
  pointer-events: none;
}

main {
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

blockquote {
  padding:10vmin;
  margin:0;
  text-align:center;
  border:20vmin solid black;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.back-image-container {
  border:20vmin dashed black;
}

.bio {
  border:10vmin double black;
  padding:5vmin 5vmin 20vmin 5vmin;
  max-width:100vmin;
  max-height:30vmin;
  overflow:scroll;
  text-align:justify;
}

.edgy {
  border:1px dashed black;
  padding-top:1vmin;
}

h3 {
  margin:0;
  padding:0;
  border:10vmin solid black;
}
.App-events {
  margin:0vmin;
  padding:5vmin;
  border:10vmin dotted black;
}
.Event-item {
  margin:3vmin;
  border: 3vmin double black;
  font-size: calc(4px + 2vmin);
  border-radius:2vmin;
  padding:2vmin;
}

.Event-date {
  display:flex;
  flex-direction: column;
  text-align:center;
  font-size: calc(10px + 3vmin);
}
.Event-title {
  display:flex;
  flex-direction: row;
  text-align:left;
  font-weight:bold;
  
}
.Event-text {
  /* border:1px solid black; */
  display:flex;
  flex-direction: row-reverse;
  text-align:right;
  font-style:italic;
  
}
.App-title {
  margin:10vmin;
  padding:10vmin;
  border: 1px dashed black;
  border-radius:10vmin;
}